'use client';

import React, { useEffect } from 'react';
import { useGameLogic } from '../hooks/useGameLogic';
import Sidebar from '../components/Sidebar';
import GameBoard from '../components/GameBoard';

export default function Home() {
  // On r√©cup√®re toute la logique depuis notre hook
  const game = useGameLogic();

  // Gestion clavier (peut aussi √™tre d√©plac√©e dans le hook si tu pr√©f√®res, mais ok ici)
  useEffect(() => {
      const handleKeyDown = (e: KeyboardEvent) => {
          if (['INPUT', 'SELECT', 'TEXTAREA'].includes((e.target as HTMLElement).tagName)) return;
          if (e.key === 'ArrowRight') game.handleNext();
          if (e.key === 'ArrowLeft') game.handlePrev();
          if (e.key === ' ') { e.preventDefault(); game.togglePlay(); }
      };
      window.addEventListener('keydown', handleKeyDown);
      return () => window.removeEventListener('keydown', handleKeyDown);
  }, [game.handleNext, game.handlePrev, game.togglePlay]);

  // Initialisation du jeu au montage
  useEffect(() => { game.startNewGame(); }, []);

  return (
    <div className="h-screen w-screen flex overflow-hidden bg-[#2c3e50] font-sans">
      <Sidebar 
          {...game} // Passe toutes les props d'un coup
          removeMission={(idx) => game.setMissions(p => p.filter((_, i) => i !== idx))} 
      />
      <GameBoard allCards={game.allCards} />
    </div>
  );
}