import React from 'react';
import { CardData } from '../types';
import Card from './Card';

export default function GameBoard({ allCards }: { allCards: CardData[] }) {
  
  // LOGIQUE DE STYLE DE CARTE
  const getCardStyle = (card: CardData) => {
      if (card.status === 'WON') {
          const winPos = ["translate(-50%, 800px)", "translate(-1200px, -50%)", "translate(-50%, -1000px)", "translate(1200px, -50%)"];
          return { transform: winPos[card.winnerIndex || 0] + " scale(0.4)", opacity: 0, zIndex: card.zIndex };
      }
      if (card.status === 'TABLE') {
          const rX = (card.tableRotation * 5) % 20 - 10;
          const rY = (card.tableRotation * 7) % 20 - 10;
          return { transform: `translate(calc(-50% + ${rX}px), calc(-50% + ${rY}px)) rotate(${card.tableRotation}deg) scale(1.1)`, opacity: 1, zIndex: card.zIndex };
      }

      const cardsInHand = allCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => {
          if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color];
          return a.value - b.value;
      });
      
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;
      const FIXED_ANGLE = 6; const xSpacing = 4.5; const yArch = 0.03; 
      const totalSpread = (total - 1) * FIXED_ANGLE;
      const rotation = (myIndex * FIXED_ANGLE) - (totalSpread / 2);
      const xOffset = rotation * xSpacing; 
      const yOffset = (rotation * rotation) * yArch; 

      const DIST_V = 220; const DIST_H = 350;
      let baseTransform = "";
      switch(card.owner) {
        case 0: baseTransform = `translate(calc(-50% + ${xOffset}px), calc(${DIST_V}px + ${yOffset}px)) rotate(${rotation}deg)`; break;
        case 1: baseTransform = `translate(calc(-${DIST_H}px - ${yOffset}px), calc(-50% + ${xOffset}px)) rotate(${90 + rotation}deg)`; break;
        case 2: baseTransform = `translate(calc(-50% - ${xOffset}px), calc(-${DIST_V}px - ${yOffset}px)) rotate(${180 + rotation}deg)`; break;
        case 3: baseTransform = `translate(calc(${DIST_H}px + ${yOffset}px), calc(-50% - ${xOffset}px)) rotate(${-90 + rotation}deg)`; break;
      }
      return { transform: baseTransform, opacity: 1, zIndex: myIndex };
  };

  return (
    <div className="w-[80%] relative bg-[#2c3e50] overflow-hidden shadow-inner flex items-center justify-center">
        <div className="absolute top-[10%] left-1/2 -translate-x-1/2 text-white/20 font-bold tracking-[0.5em]">NORD</div>
        <div className="absolute bottom-[10%] left-1/2 -translate-x-1/2 text-white/20 font-bold tracking-[0.5em]">SUD</div>
        <div className="absolute left-[5%] top-1/2 -translate-y-1/2 text-white/20 font-bold tracking-[0.5em] [writing-mode:vertical-lr] rotate-180">OUEST</div>
        <div className="absolute right-[5%] top-1/2 -translate-y-1/2 text-white/20 font-bold tracking-[0.5em] [writing-mode:vertical-lr]">EST</div>
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-48 h-48 rounded-full border-2 border-white/5 bg-white/5 pointer-events-none"></div>

        {allCards.map((card) => {
            const style = getCardStyle(card);
            const speed = card.status === 'WON' ? '1000ms' : '600ms';
            return (
                <div key={card.id} className="absolute top-1/2 left-1/2 transition-all ease-in-out" style={{ ...style, width: '5rem', transitionProperty: 'transform, opacity', transitionDuration: speed }}>
                    <Card color={card.color} value={card.value} className="w-20 aspect-[2/3] shadow-xl border border-black/20" />
                </div>
            );
        })}
      </div>
  );
}