import React from 'react';
import { MissionUI, SolverStats, CardData } from '../types';
import { MISSION_LOGBOOK } from '../data/missions';
import TrickHistory from './TrickHistory'; // Assure-toi d'avoir boug√© TrickHistory aussi

type SidebarProps = {
    startNewGame: () => void;
    bookMissionId: number;
    loadMissionFromBook: (id: number) => void;
    missions: MissionUI[];
    setMissions: React.Dispatch<React.SetStateAction<MissionUI[]>>;
    shuffleOwners: () => void;
    shuffleCards: () => void;
    autoFindSolution: () => void;
    isAutoFinding: boolean;
    removeMission: (index: number) => void; // √Ä d√©finir dans le parent ou ici
    launchSolver: () => void;
    isThinking: boolean;
    stats: SolverStats | null;
    currentMoveIndex: number;
    isPlaying: boolean;
    togglePlay: () => void;
    handlePrev: () => void;
    handleNext: () => void;
    logs: string[];
    allCards: CardData[];
};

export default function Sidebar(props: SidebarProps) {
    const { 
        startNewGame, bookMissionId, loadMissionFromBook, missions, setMissions,
        shuffleOwners, shuffleCards, autoFindSolution, isAutoFinding, 
        launchSolver, isThinking, stats, currentMoveIndex, isPlaying, togglePlay, handlePrev, handleNext, logs, allCards
    } = props;

    // Helper local pour la suppression
    const removeMission = (index: number) => setMissions(prev => prev.filter((_, i) => i !== index));
    const getPlayerName = (idx: number) => ['Sud', 'Ouest', 'Nord', 'Est'][idx];

    return (
        <div className="w-[20%] min-w-[250px] bg-gray-900 border-r border-gray-700 flex flex-col z-50 shadow-2xl h-full">
            <div className="p-4 border-b border-gray-800 shrink-0">
                <h1 className="text-xl font-bold text-white">The Crew <span className="text-purple-500">Solver</span></h1>
            </div>

            <div className="flex-1 overflow-y-auto p-4 scrollbar-thin scrollbar-thumb-gray-700 scrollbar-track-transparent">
                <div className="flex flex-col gap-3 mb-6">
                    <button onClick={startNewGame} className="bg-gray-800 hover:bg-gray-700 text-white py-3 rounded font-bold text-sm border border-gray-600">
                        üîÑ Nouvelle Donne
                    </button>

                    {/* ZONE GESTION MISSIONS */}
                    <div className="bg-black/20 p-3 rounded border border-white/10 flex flex-col gap-3">
                         {/* SELECTEUR LIVRE */}
                         <div>
                            <h3 className="text-xs font-bold text-gray-400 mb-1 uppercase flex items-center gap-2">
                                üìñ Livre de Bord
                                {bookMissionId > 0 && <span className="text-purple-400 text-[10px]">{bookMissionId}</span>}
                            </h3>
                            <select 
                                className="w-full bg-gray-800 text-white text-xs p-2 rounded border border-gray-600 outline-none focus:border-purple-500"
                                value={bookMissionId}
                                onChange={(e) => loadMissionFromBook(Number(e.target.value))}
                            >
                                <option value="0">-- Choisir une mission --</option>
                                {MISSION_LOGBOOK.map(m => (
                                    <option key={m.id} value={m.id}>
                                        Mission {m.id} ({m.slots.length} cartes)
                                    </option>
                                ))}
                            </select>
                            {/* BOUTONS MIX */}
                            {bookMissionId > 0 && (
                                <div className="mt-2">
                                    <p className="text-[9px] text-gray-400 italic leading-tight px-1 mb-2">
                                        {MISSION_LOGBOOK.find(m => m.id === bookMissionId)?.description}
                                    </p>
                                    <div className="flex gap-1">
                                        <button onClick={shuffleOwners} className="flex-1 bg-gray-700 hover:bg-gray-600 text-[9px] text-white py-1 rounded border border-gray-600 active:scale-95 transition">üë§ Mix Joueurs</button>
                                        <button onClick={shuffleCards} className="flex-1 bg-gray-700 hover:bg-gray-600 text-[9px] text-white py-1 rounded border border-gray-600 active:scale-95 transition">üÉè Mix Cartes</button>
                                        <button onClick={autoFindSolution} disabled={isAutoFinding} className={`flex-2 py-3 rounded text-[10px] font-bold border transition flex items-center justify-center gap-2 shadow-lg ${isAutoFinding ? 'bg-purple-900/80 border-purple-500 text-purple-200 animate-pulse cursor-wait' : 'bg-gradient-to-r from-purple-700 to-indigo-700 hover:from-purple-600 hover:to-indigo-600 text-white border-indigo-500'}`}>
                                            {isAutoFinding ? <>üé≤ Recherche...</> : <>üé∞ Trouver GAGNANT</>}
                                        </button>
                                    </div>
                                </div>
                            )}
                            <div className="h-[1px] bg-white/10 mt-2"></div>
                        </div>

                        {/* LISTE DES SLOTS */}
                        <div className="space-y-2 pr-1">
                            {missions.map((m, i) => {
                                const targetId = `${m.cardColor}-${m.cardValue}`;
                                const targetCard = allCards.find(c => c.id === targetId);
                                const isAccomplished = targetCard?.status === 'WON' && targetCard?.winnerIndex === m.ownerIndex;
                                const isFailed = targetCard?.status === 'WON' && targetCard?.winnerIndex !== undefined && targetCard?.winnerIndex !== m.ownerIndex;

                                return (
                                <div key={i} className={`p-2 rounded border transition-colors ${isAccomplished ? 'bg-green-900/30 border-green-500/50' : isFailed ? 'bg-red-900/30 border-red-500/50' : 'bg-gray-800 border-gray-600'}`}>
                                    <div className="flex justify-between items-center mb-1">
                                        <div className="flex items-center gap-2">
                                            <span className="text-xs">{isAccomplished ? '‚úÖ' : isFailed ? '‚ùå' : 'üéØ'}</span>
                                            {m.token !== 'None' && (<span className="bg-purple-600 text-white px-1.5 rounded text-[9px] font-bold">{m.token === 'Omega' ? 'Œ©' : m.token}</span>)}
                                        </div>
                                        <button onClick={() => removeMission(i)} className="text-gray-500 hover:text-red-400 text-xs">‚úï</button>
                                    </div>
                                    <div className="flex gap-1">
                                        <select className="bg-black/30 text-white text-[10px] p-1 rounded w-16 outline-none" value={m.ownerIndex} onChange={(e) => { const newMissions = [...missions]; newMissions[i].ownerIndex = Number(e.target.value); setMissions(newMissions); }}>
                                            <option value={0}>Sud</option><option value={1}>Ouest</option><option value={2}>Nord</option><option value={3}>Est</option>
                                        </select>
                                        <select className="bg-black/30 text-white text-[10px] p-1 rounded w-10 text-center outline-none" value={m.cardValue} onChange={(e) => { const newMissions = [...missions]; newMissions[i].cardValue = Number(e.target.value); setMissions(newMissions); }}>
                                            {[1,2,3,4,5,6,7,8,9].map(n => <option key={n} value={n}>{n}</option>)}
                                        </select>
                                        <select className={`text-[10px] p-1 rounded flex-1 font-bold outline-none ${m.cardColor === 'Blue' ? 'bg-blue-900/50 text-blue-200' : m.cardColor === 'Green' ? 'bg-green-900/50 text-green-200' : m.cardColor === 'Pink' ? 'bg-pink-900/50 text-pink-200' : m.cardColor === 'Yellow' ? 'bg-yellow-900/50 text-yellow-200' : 'bg-gray-700 text-white'}`} value={m.cardColor} onChange={(e) => { const newMissions = [...missions]; newMissions[i].cardColor = e.target.value; setMissions(newMissions); }}>
                                            <option value="Blue">Bleu</option><option value="Green">Vert</option><option value="Pink">Rose</option><option value="Yellow">Jaune</option><option value="Rocket">Fus√©e</option>
                                        </select>
                                    </div>
                                </div>
                            )})}
                            {missions.length === 0 && <div className="text-gray-500 text-[10px] text-center py-2">Aucune mission active</div>}
                        </div>
                        <button onClick={() => setMissions(prev => [...prev, { cardColor: 'Blue', cardValue: 1, ownerIndex: 0, token: 'None' }])} className="w-full border border-dashed border-gray-600 text-gray-400 hover:text-white text-[10px] py-1 rounded transition-colors hover:border-gray-400">
                            + Ajouter slot manuel
                        </button>
                    </div>

                    <button onClick={launchSolver} disabled={isThinking} className={`py-3 rounded font-bold transition text-sm ${isThinking ? 'bg-purple-900 text-purple-300' : 'bg-purple-600 hover:bg-purple-500 text-white'}`}>
                        {isThinking ? 'Calcul en cours...' : '‚ö°Ô∏è Lancer Solver'}
                    </button>
                </div>

                {/* CONTROLES LECTURE */}
                {stats && (
                    <div className="bg-black/40 p-3 rounded-lg border border-white/10 mb-4 animate-fade-in">
                        <div className="flex justify-between items-center mb-2 text-xs text-gray-400 font-mono">
                            <span>Coup {Math.ceil(currentMoveIndex + 1)} / {stats.solution_steps.length}</span>
                            <span>{isPlaying ? '‚ñ∂ LECTURE' : '‚è∏ PAUSE'}</span>
                        </div>
                        <div className="w-full h-1.5 bg-gray-700 rounded-full mb-4 overflow-hidden"><div className="h-full bg-gradient-to-r from-purple-500 to-pink-500 transition-all duration-300" style={{ width: `${((currentMoveIndex + 1) / stats.solution_steps.length) * 100}%` }}></div></div>
                        <div className="flex gap-2">
                            <button onClick={handlePrev} className="flex-1 bg-gray-700 hover:bg-gray-600 text-white py-2 rounded text-lg active:scale-95">‚èÆ</button>
                            <button onClick={togglePlay} className={`flex-1 py-2 rounded text-lg font-bold active:scale-95 text-white ${isPlaying ? 'bg-yellow-600 hover:bg-yellow-500' : 'bg-green-600 hover:bg-green-500'}`}>{isPlaying ? '‚è∏' : '‚ñ∂'}</button>
                            <button onClick={handleNext} className="flex-1 bg-gray-700 hover:bg-gray-600 text-white py-2 rounded text-lg active:scale-95">‚è≠</button>
                        </div>
                    </div>
                )}

                {/* RAPPORT SOLUTION */}
                {stats && stats.solutionFound && (
                    <div className="flex-1 overflow-y-auto min-h-[150px] mb-4 pr-1 scrollbar-thin scrollbar-thumb-gray-700">
                        <TrickHistory steps={stats.solution_steps} />
                    </div>
                )}

                {/* LOGS */}
                <div className="bg-black/40 rounded p-2 font-mono text-[10px] text-gray-400 border border-white/5 min-h-[100px]">
                    {logs.map((l, i) => <div key={i} className="mb-1">{l}</div>)}
                </div>
            </div>
        </div>
    );
}